import styled from "styled-components";
import { formatCurrency } from "../utils";
import { useContext } from "react";
import CartContext from "../Context/Cart/CartContext";
import { Link } from "react-router-dom";
import './Checkout.css';

const Checkout = () => {
  const { clearCart, handleCheckout, itemCount, total } =
    useContext(CartContext);

  return (
    <Wrapper>
      <p className="text-white">Total Items:</p>
      <h4 className="text-white">{itemCount}</h4>
      <p className="text-white">Total Payment:</p>
      <h4 className="text-white">{formatCurrency(total)}</h4>
      <hr />
      <div>
        <Link onClick={handleCheckout} className="checkout" to="/cart/Checkout">CHECKOUT</Link>
        <ClearBtn className="ml-5" onClick={clearCart}>
          CLEAR
        </ClearBtn>
      </div>
    </Wrapper>
  );
};

// Styled Components

const Wrapper = styled.div`
  margin-top: 3rem;
  border: 1px solid #000;
  padding: 1.5rem;
  border-color: white;

  p,
  h4 {
    margin: 1rem;
    font-size: large;
  }
`;

const CheckBtn = styled.button`
  color: #fff;
  background-color: green;
  border: 1px solid #1a1a1a;
  padding: 0.5rem 1rem;
  line-height: 1.5;
  font-size: 10px;
  border-radius: 0;
  text-transform: uppercase;
  cursor: pointer;
  outline: none;
`;

const ClearBtn = styled(CheckBtn)`
  background: white;
  color: #000;

  &:hover {
    background-color: red;
    color: #fff;
  }
`;
export default Checkout;
