import axios from "axios";

const apiKey = "c9b940af29msh75a51aef55d2896p1fae7cjsn7f32975281b2";
const apiUrl = "https://kohls.p.rapidapi.com/products/list";
const limit = 100;
const dimensionValueID = "Product:Athletic Shoes & Sneakers";

const getProducts = async (search) => {
  const options = {
    method: "GET",
    url: `${apiUrl}?limit=${limit}&keyword=${encodeURIComponent(search)}&${dimensionValueID}`,
    headers: {
      "X-RapidAPI-Key": apiKey,
      "X-RapidAPI-Host": "kohls.p.rapidapi.com",
    },
  };
  // console.log(options.url);

  try {
    const response = await axios.request(options);
    // console.log(response);
    // console.log(response.data.payload.products);
    return response.data.payload.products;
  } catch (error) {
    console.error(error);
    return [];
  }
};

export const fullShop = async (search = "sneakers") => {
  try {
    const products = await getProducts(search);

    let resultProducts = [];

    if (products.length >= 24) {
      resultProducts = products.slice(0, 24);
    } else {
      resultProducts = products;
      const remainingProductsCount = 24 - products.length;
      const remainingProducts = products.slice(0, remainingProductsCount);
      resultProducts = resultProducts.concat(remainingProducts);
    }

    return resultProducts.map((item) => ({
      image: item.image.url,
      name: item.productTitle,
      id: item.webID,
      rating: item.rating.avgRating,
      price: item.prices[0].regularPrice.minPrice,
    }));
  } catch (error) {
    console.error(error);
    return [];
  }
};
