import { useContext } from "react";
import CartContext from "../Context/Cart/CartContext";
import styled from "styled-components";
import { formatCurrency } from "../utils";
import TrashIcon from "../assets/icons/trash-outline.svg";
import Plus from "../assets/icons/add-circle-outline.svg";
import Minus from "../assets/icons/remove-circle-outline.svg";
import { Splide, SplideSlide } from "@splidejs/react-splide";
import "@splidejs/splide/dist/css/themes/splide-default.min.css";

const CartItem = ({ product }) => {
  const { removeFromCart, increase, decrease } = useContext(CartContext);

  return (
    <SplideSlide>
      <SingleCartItem className="CartLL">
        <div className="xl:w-3/5 flex flex-col sm:flex-row xl:flex-col justify-center items-center py-7 sm:py-0 xl:py-10 px-12 xl:w-full">
          <div className="flex flex-col justify-start items-start w-full space-y-4">
            <p className="text-md md:text-2md leading-normal text-black-800 dark:text-black-50">
              {product.name}
            </p>
            <p className="text-base font-semibold leading-none text-black-600 dark:text-black">
              {formatCurrency(product.price)}
            </p>
          </div>
          <div className="mt-6 sm:mt-0 xl:my-10 xl:px-20 w-52 sm:w-96 xl:w-auto">
            <img src={product.image} alt={product.name} />
          </div>
        </div>

        {/* Buttons */}
        <BtnContainer>
          <button
            onClick={() => increase(product)}
            className="btn btn-primary px-6 py-2 "
          >
            <Icon src={Plus} alt="" />
          </button>

          <div>
            <p>Qty: {product.quantity}</p>
          </div>

          {product.quantity > 1 && (
            <button
              onClick={() => decrease(product)}
              className="btn px-6 py-2 "
            >
              <Icon src={Minus} alt="" />
            </button>
          )}

          {product.quantity === 1 && (
            <button
              onClick={() => removeFromCart(product)}
              className="btn px-6 py-2 "
            >
              <Icon src={TrashIcon} alt="" />
            </button>
          )}
        </BtnContainer>
      </SingleCartItem>
    </SplideSlide>
  );
};

//Styled Components

const SingleCartItem = styled.div`
  border-bottom: 2px solid gray;
  padding: 10px 0;
  margin-top: 10px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100%;

  &:nth-child(1) {
    border-top: 2px solid gray;
  }
`;

const BtnContainer = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-around;
`;

const Icon = styled.img`
  width: 3rem;
  height: auto;
  @media only screen and(max-width:768px) {
    button {
      display: block;
    }
  }
`;

export default CartItem;
