import React, { useState, useEffect } from 'react';
import { Link, useNavigate } from "react-router-dom";
import CartIcon from "../assets/icons/cart.svg";
import CartContext from "../Context/Cart/CartContext";
import styled from "styled-components";
import { useContext } from "react";
import Logo from "../foto/logo.png";
import GoogleSignIn from "./Signin";

function handleMenuToggle() {
    const menu = document.querySelectorAll('.navbar-menu');

    for (var j = 0; j < menu.length; j++) {
        menu[j].classList.toggle('hidden');
    }
}

const Hamburger = ({ User }) => {
    useEffect(() => {
        const burger = document.querySelectorAll('.navbar-burger');
        const close = document.querySelectorAll('.navbar-close');
        const backdrop = document.querySelectorAll('.navbar-backdrop');

        if (burger.length && close.length && backdrop.length) {
            for (var i = 0; i < burger.length; i++) {
                burger[i].addEventListener('click', handleMenuToggle);
            }

            for (i = 0; i < close.length; i++) {
                close[i].addEventListener('click', handleMenuToggle);
            }

            for (i = 0; i < backdrop.length; i++) {
                backdrop[i].addEventListener('click', handleMenuToggle);
            }
        }

        // Clean up the event listeners when the component unmounts
        return () => {
            if (burger.length && close.length && backdrop.length) {
                for (i = 0; i < burger.length; i++) {
                    burger[i].removeEventListener('click', handleMenuToggle);
                }

                for (i = 0; i < close.length; i++) {
                    close[i].removeEventListener('click', handleMenuToggle);
                }

                for (i = 0; i < backdrop.length; i++) {
                    backdrop[i].removeEventListener('click', handleMenuToggle);
                }
            }
        };
    }, []);

    const toTop = () => {
        window.scrollTo({ top: 0, behavior: "smooth" });
    };

    const { cartItems } = useContext(CartContext);
    const [toggle, setToggle] = useState(false);
    // const [isMenuOpen, setIsMenuOpen] = useState(false);
    const navigate = useNavigate();

    return (
        <div>
            <nav className="relative px-4 py-4 flex justify-between items-center bg-white">
                <div className="ml-5 text-3xl font-bold leading-none">
                    <Link
                        to="/"
                        className="flex order-first"
                        onClick={toTop}
                    >
                        <img className="h-12" src={Logo} alt="logo" />
                    </Link>
                </div>
                <div className="lg:hidden">
                    <button className="navbar-burger flex items-center text-blue-600 p-3">
                        <svg className="block h-4 w-4 fill-current" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg">
                            <title>Mobile menu</title>
                            <path d="M0 3h20v2H0V3zm0 6h20v2H0V9zm0 6h20v2H0v-2z"></path>
                        </svg>
                    </button>
                </div>
                <ul className="hidden absolute top-1/2 left-1/2 transform -translate-y-1/2 -translate-x-1/2 lg:flex lg:mx-auto lg:flex lg:items-center lg:w-auto lg:space-x-6">
                    <li>
                        <Link to="/" className="block px-4 py-2 text-gray-800 rounded-lg focus:outline-none focus:ring focus:ring-black-300">
                            Home
                        </Link>
                    </li>
                    <li>
                        <Link to="/Shop" className="block px-4 py-2 text-gray-800 rounded-lg focus:outline-none focus:ring focus:ring-black-300">
                            Shop
                        </Link>
                    </li>
                    <li>
                        <Link to="/About-Us" className="block px-4 py-2 text-gray-800 rounded-lg focus:outline-none focus:ring focus:ring-black-300">
                            About Us
                        </Link>
                    </li>
                    <li>
                        <button
                            className="block px-4 py-2 text-gray-800 rounded-lg focus:outline-none focus:ring focus:ring-black-300"
                            onClick={() => navigate(-1)}
                        >
                            Go Back
                        </button>
                    </li>
                </ul>
                <div className="hidden lg:inline-block lg:ml-auto lg:mr-3 py-2 px-6 bg-gray-50 text-sm text-gray-900 font-bold rounded-xl transition duration-200">
                    <div className="google-sign">
                        {User ? (
                            <div className="border-0 py-1 px-3 focus:outline-none rounded text-base mt-4 md:mt-0">
                                <p className="mr-2">{User.displayName}</p>
                                <img
                                    src={User.photoURL}
                                    alt="Profile"
                                    className="w-8 h-8 rounded-full"
                                />
                            </div>
                        ) : (
                            <GoogleSignIn SignIn={(displayName, photoURL) => { }} />
                        )}
                    </div>
                </div>
                <div className="hidden lg:inline-block py-2 px-6 hover:bg-gray-600 text-sm text-white font-bold rounded-xl transition duration-200">
                    <Link to="./cart" onClick={() => setToggle(!toggle)}>
                        <img src={CartIcon} alt="Shopping cart" />
                        {cartItems.length > 0 && <CartCircle>{cartItems.length}</CartCircle>}
                        Cart
                    </Link>
                </div>
            </nav>
            <div className="navbar-menu relative z-50 hidden">
                <div className="navbar-backdrop fixed inset-0 bg-gray-800 opacity-25"></div>
                <nav className="fixed top-0 left-0 bottom-0 flex flex-col w-5/6 max-w-sm py-6 px-6 bg-white border-r overflow-y-auto">
                    <div className="flex items-center mb-8">
                        <div className="mr-auto text-3xl font-bold leading-none">
                            <Link
                                to="/"
                                className="flex order-first"
                                onClick={toTop}
                            >
                                <img className="h-12" src={Logo} alt="logo" />
                            </Link>
                        </div>
                        <button className="navbar-close">
                            <svg className="h-6 w-6 text-gray-400 cursor-pointer hover:text-gray-500" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M6 18L18 6M6 6l12 12"></path>
                            </svg>
                        </button>
                    </div>
                    <div>
                        <ul>
                            <li className="mb-1">
                                <Link to="/" className="block px-4 py-2 text-gray-800 rounded-lg focus:outline-none focus:ring focus:ring-black-300">
                                    Home
                                </Link>
                            </li>
                            <li className="mb-1">
                                <Link to="/Shop" className="block px-4 py-2 text-gray-800 rounded-lg focus:outline-none focus:ring focus:ring-black-300">
                                    Shop
                                </Link>
                            </li>
                            <li className="mb-1">
                                <Link to="/About-Us" className="block px-4 py-2 text-gray-800 rounded-lg focus:outline-none focus:ring focus:ring-black-300">
                                    About Us
                                </Link>
                            </li>
                            <li className="mb-1">
                                <button
                                    className="block px-4 py-2 text-gray-800 rounded-lg focus:outline-none focus:ring focus:ring-black-300"
                                    onClick={() => navigate(-1)}
                                >
                                    Go Back
                                </button>
                            </li>
                            <li>
                                <Link className="block px-4 py-2 text-gray-800 rounded-lg focus:outline-none focus:ring focus:ring-black-300" to="/cart" onClick={() => setToggle(!toggle)}>
                                    <img className="object-scale-down h-8 " src={CartIcon} alt="Shopping cart" />
                                    {cartItems.length > 0 && <CartCircle>{cartItems.length}</CartCircle>}
                                    Cart
                                </Link>
                            </li>
                        </ul>
                    </div>
                    <div className="mt-auto">
                        <div className="pt-6">
                            <div className="block px-4 py-2 mb-3 leading-loose text-xs text-center font-semibold leading-none bg-gray-50 hover:bg-gray-100 rounded-xl">
                                <Link
                                    to="/"
                                    className="flex order-first lg:order-none lg:w-1/5 title-font font-medium text-gray-900 items-center lg:items-center lg:justify-center"
                                    onClick={toTop}
                                >
                                </Link>
                                <div className="google-sign">
                                    {User ? (
                                        <div className="border-0 px-3 focus:outline-none rounded text-base mt-4 md:mt-0">
                                            <p className="mr-2">{User.displayName}</p>
                                            <img
                                                src={User.photoURL}
                                                alt="Profile"
                                                className="w-8 h-8 rounded-full"
                                            />
                                        </div>
                                    ) : (
                                        <GoogleSignIn SignIn={(displayName, photoURL) => { }} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <p className="my-4 text-xs text-center text-gray-400">
                            <span>Copyright  2023</span>
                        </p>
                    </div>
                </nav>
            </div>
        </div>
    );
}

const CartCircle = styled.div`
  position: absolute;
  background-color: #13122e;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.85em;

  @media (max-width: 500px) {
    position: initial;
  }
`;

export default Hamburger;